import React, { useState, useEffect} from 'react';
import axios from 'axios';

function POAPs() {
  const [input, setInput] = useState('');
  const [isVisible, setIsVisible] = useState(false);
  const [loading, setLoading] = useState(false);

  const [poaps, setPoaps] = useState([
      {
        "participated": true,
        "collection": {
            "event": {               
                "image_url": "https://assets.poap.xyz/335d8e5a-920f-4c62-aa41-d8745f350a26.png",               
            },
            "tokenId": "7189761",
        }
      },
      {
            "participated": true,
            "collection": {
                "event": {               
                    "image_url": "https://assets.poap.xyz/7409f90a-8b44-4aee-be90-ed622b794177.png",               
                },
                "tokenId": "7201587",
            }
      },
      {
            "participated": true,
            "collection": {
                "event": {               
                    "image_url": "https://assets.poap.xyz/14ec1c82-0e97-4568-9e8c-fba02111c4e7.png",               
                },
                "tokenId": "7200078",
            }
      },
      {
            "participated": true,
            "collection": {
                "event": {               
                    "image_url": "https://assets.poap.xyz/635dc31f-985a-4536-9203-57c2f24ba493.png",               
                },
                "tokenId": "7284595",
            }
      }
  ]);

  const handleSearch = async () => {
    setPoaps([]);
    setLoading(true);

    try {
      const response = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input}/badges`);
      const ownedPoaps = response.data.poaps.filter(poap => poap.participated && poap.collection);
      setPoaps(ownedPoaps);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      console.error('Error fetching data:', error);
      alert('Please check the ENS or address and try again.');
    }
  };

  useEffect(() => {
    if (poaps.length === 0) {
      setTimeout(() => setIsVisible(true), 1500);
    } else {
      setIsVisible(false);
    }
  }, [poaps]);

  return (
    <div className="show">
      <input
        type="text"
        value={input}
        onChange={(e) => setInput(e.target.value.trim().toLowerCase())}
        placeholder="Enter ENS or address"
        className="input"
      />
      <button onClick={handleSearch}>Check</button>
      {loading ? <div className="loading">Loading...</div> :
        <div className="poap-cards" style={{ marginTop: '20px' }}>
          {poaps.length > 0 ? (
            poaps.map((poap, index) => (
              <a
                key={index}
                href={`https://collectors.poap.xyz/token/${poap.collection.tokenId}`}
                target="_blank"
                rel="noopener noreferrer"
                className="poap-card"
              >
                <img
                  src={poap.collection.event.image_url}
                  alt={poap.collection.event.name}
                  style={{
                    width: '200px',
                    height: '200px',
                    borderRadius: '50%',
                    margin: '0 auto',
                  }}
                />
                <p style={{ textAlign: 'center', marginTop: '10px', fontWeight: 'bold', color: 'black' }}>
                  {poap.collection.tokenId}
                </p>
              </a>
            ))
          ) : (
            isVisible && <p style={{ textAlign: 'center', marginTop: '10px', fontWeight: 'bold', color: 'red', fontSize: '1.5em' }}>No Poap Found</p> 
          )}
        </div>
      }
    </div>
  );
}

export default POAPs; 