import React, { useState, useEffect } from 'react';
import axios from 'axios';
import WordCloud from 'react-wordcloud';

function WordCloudPage() {
  const [words, setWords] = useState([]);
  const [fontSizes, setFontSizes] = useState([20, 80]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get('https://api.ethfollow.xyz/api/v1/leaderboard/ranked?limit=300&sort=followers&direction=desc');
        const leaderboardData = response.data.results;

        const wordData = leaderboardData
          .filter(entry => entry.name)
          .map(entry => ({
            text: entry.name,
            value: parseInt(entry.followers, 10),
            link: `https://efp.app/${entry.name}`
          }));

        setWords(wordData);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
  }, []);

  useEffect(() => {
    const updateFontSizes = () => {
      if (window.innerWidth < 600) {
        setFontSizes([10, 30]);
      } else {
        setFontSizes([20, 80]);
      }
    };

    updateFontSizes();

    window.addEventListener('resize', updateFontSizes);

    return () => {
      window.removeEventListener('resize', updateFontSizes);
    };
  }, []);

  const handleWordClick = (word) => {
    window.open(word.link, '_blank');
  };

  return (
    <div className="wordcloud-container">
      <div className="wordcloud">
        <WordCloud 
          words={words} 
          options={{ 
            rotations: 0, 
            padding: 2,
            scale: 'log',
            fontSizes: fontSizes,
            spiral: 'archimedean',
          }}
          callbacks={{
            onWordClick: handleWordClick
          }} 
        />
      </div>
    </div>
  );
}

export default WordCloudPage; 