import React, { useState } from 'react';
import axios from 'axios';

function EFPDash() {
  const [input, setInput] = useState('');
  const [data, setData] = useState(null);
  const [name, setName] = useState(null);
  const [avatar, setAvatar] = useState(null);
  const [loading, setLoading] = useState(false);

  const handleSearch = async () => {
    setData(null);
    setName(null);
    setAvatar(null);
    setLoading(true);

    try {
      const accountResponse = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input}/account`);
      const detailsResponse = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input}/details`);
      const statsResponse = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input}/stats`);
      const qrResponse = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input}/qr`, {
        headers: { 'Accept': 'image/svg+xml' }
      });
      function shortAddress(address) {
        return `${address.slice(0, 6)}...${address.slice(-4)}`;
      }

      if (!accountResponse.data.ens.name) {
        setName(shortAddress(accountResponse.data.address));
        setAvatar('https://docs.efp.app/logo.png');
      } else {
        setName(accountResponse.data.ens.name);
        setAvatar(accountResponse.data.ens.avatar);
      }

      setData({
        details: detailsResponse.data,
        stats: statsResponse.data,
        qr: qrResponse.data,
      });
      setLoading(false);
    } catch (error) {
      setLoading(false);
      console.error('Error fetching data:', error);
      alert('Please check the ENS or address and try again.');
    }
  };

  return (
    <div className="show">
      <input
        type="text"
        value={input}
        onChange={(e) => setInput(e.target.value.trim().toLowerCase())}
        placeholder="Enter ENS or address"
        className="input"
      />
      <button onClick={handleSearch}>Check</button>
      {loading ? <div className="loading">Loading...</div> :
        (data && (
          <div className="card">
            <div className="card-content" style={{ textAlign: 'center', flex: 1 }}>
              <div style={{display: 'flex', flexDirection: 'column', alignItems: 'center'}}>
                <img
                  src={avatar}
                  alt="Avatar"
                  className="avatar"
                />
                <h2 className="ensname">@{name}</h2>
              </div>
              <div className="data-cards">
                <div className="data-card" style={{ backgroundColor: '#ee5253', fontSize: '0.9rem'}}>
                  <span>Following</span>
                  <span>{data.stats.following_count}</span>
                </div>
                <div className="data-card" style={{ backgroundColor: '#ee5253', fontSize: '0.9rem'}}>
                  <span>Followers</span>
                  <span>{data.stats.followers_count}</span>
                </div>
              </div>
              <h2 style={{ width: '100%', textAlign: 'left', fontSize: '1.2em', fontWeight: 'bolder', margin: '20px 0'}}>Ranks</h2>
              <div className="data-cards">              
                <div className="data-card" style={{ backgroundColor: '#ffa502', fontSize: '0.85rem'}}>
                  <span>Following</span>
                  <span>{data.details.ranks.following_rank}</span>
                </div>
                <div className="data-card" style={{ backgroundColor: '#ffa502', fontSize: '0.85rem'}}>
                  <span>Followers</span>
                  <span>{data.details.ranks.followers_rank}</span>
                </div>
                <div className="data-card" style={{ backgroundColor: '#ffa502', fontSize: '0.85rem'}}>
                  <span>Mutuals</span>
                  <span>{data.details.ranks.mutuals_rank}</span>
                </div>
                <div className="data-card" style={{ backgroundColor: '#ffa502', fontSize: '0.85rem'}}>
                  <span>Blocks</span>
                  <span>{data.details.ranks.blocks_rank}</span>
                </div>
                <div className="data-card" style={{ backgroundColor: '#ffa502', fontSize: '0.85rem'}}>
                  <span>Top8</span>
                  <span>{data.details.ranks.top8_rank}</span>
                </div>
              </div>
            </div>
            <div className="card-content" style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flex: 1 }}>
              <a href={`https://efp.app/${input}`} target="_blank" rel="noopener noreferrer">              
                <div dangerouslySetInnerHTML={{ __html: data.qr }} style={{ width: '100%', borderRadius: '20px'}} />
              </a>
            </div>
          </div>
        ))
      }
    </div>
  );
}

export default EFPDash; 