import React, { useState } from 'react';
import axios from 'axios';

function Connection() {
  const [input1, setInput1] = useState('');
  const [input2, setInput2] = useState('');
  const [qr1, setQr1] = useState(null);
  const [qr2, setQr2] = useState(null);
  const [connectionStatus, setConnectionStatus] = useState(null);
  const [loading, setLoading] = useState(false);

  const handleSearch = async () => {
    // 清空之前的结果
    setQr1(null);
    setQr2(null);
    setConnectionStatus(null);
    setLoading(true);

    try {
      // 获取第一个地址的QR图片
      const qrResponse1 = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input1}/qr`, {
        headers: { 'Accept': 'image/svg+xml' }
      });
      setQr1(qrResponse1.data);

      // 获取第二个地址的QR图片
      const qrResponse2 = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input2}/qr`, {
        headers: { 'Accept': 'image/svg+xml' }
      });
      setQr2(qrResponse2.data);

      // 检查关注关系    
      let response = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input1}/${input2}/followerState`);
      let responseVerse = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input2}/${input1}/followerState`);
      if (!response.data.state.follow){
        if (!responseVerse.data.state.follow){
          setConnectionStatus('none');
        } else {
          setConnectionStatus('right');
        }
      } else {
        if (!responseVerse.data.state.follow){
          setConnectionStatus('left');
        } else {
          setConnectionStatus('both');
        }
      }

      setLoading(false);
    } catch (error) {
      setLoading(false);
      console.error('Error fetching data:', error);
      alert('Please check the ENS or address and try again.');
    }
  };

  return (
    <div className="show">
      <input
        type="text"
        value={input1}
        onChange={(e) => setInput1(e.target.value.trim().toLowerCase())}
        placeholder="Enter first ENS or address"
        className="input"
      />
      <input
        type="text"
        value={input2}
        onChange={(e) => setInput2(e.target.value.toLowerCase())}
        placeholder="Enter second ENS or address"
        className="input"
      />
      <button onClick={handleSearch}>Check</button>
      {loading ? <div className="loading">Loading...</div> :
          (connectionStatus && (
            <div className="connection">
              <a href={`https://efp.app/${input1}`} target="_blank" rel="noopener noreferrer" className="qrCard">
                {qr1 && <div dangerouslySetInnerHTML={{ __html: qr1 }} className="qr" />}
              </a>
              <img src={`${process.env.PUBLIC_URL}/arrow/${connectionStatus}.svg`} alt="Arrow" className="arrow" />
              <a href={`https://efp.app/${input2}`} target="_blank" rel="noopener noreferrer" className="qrCard">
                {qr2 && <div dangerouslySetInnerHTML={{ __html: qr2 }} className="qr" />}
              </a>
            </div>
          ))
      }
    </div>
  );
}

export default Connection; 