import React, { useState, useEffect } from 'react';
import axios from 'axios';

function ListDash() {
  const [input, setInput] = useState('');
  const [data, setData] = useState(null);
  const [listId, setListID] = useState(null);
  const [loading, setLoading] = useState(false);

  const handleSearch = async () => {
    setData(null);
    setListID(null);
    setLoading(true);

    try {
      const detailsResponse = await axios.get(`https://api.ethfollow.xyz/api/v1/lists/${input}/details`);
      const statsResponse = await axios.get(`https://api.ethfollow.xyz/api/v1/lists/${input}/stats?live=true`);
      const imageResponse = await axios.get(`https://api.ethfollow.xyz/api/v1/token/image/${input}`, {
        headers: { 'Accept': 'image/svg+xml' }
      });


      setListID(input);
      setData({
        details: detailsResponse.data,
        stats: statsResponse.data,
        image: imageResponse.data,
      });
      setLoading(false);
    } catch (error) {
      console.error('Error fetching data:', error);
      if (error.response && error.response.status === 404) {
        setListID(input);
        setData({
          error: 'has not been registered yet!'
        });
        setLoading(false);
      } else {
        setLoading(false);
        alert('Please check the list ID and try again.');
      }
    }
  };

  useEffect(() => {
    if (data && data.image) {
      const parser = new DOMParser();
      const svgDoc = parser.parseFromString(data.image, 'image/svg+xml');
      const svgElement = svgDoc.querySelector('svg');
      if (svgElement) {
        svgElement.setAttribute('width', '100%');
        svgElement.setAttribute('height', 'auto');
        setData(prevData => ({
          ...prevData,
          image: svgElement.outerHTML
        }));
      }
    }
  }, [data]);

  return (
    <div className="show">
      <input
        type="text"
        value={input}
        onChange={(e) => setInput(e.target.value.trim().toLowerCase())}
        placeholder="Enter list ID"
        className="input"
      />
      <button onClick={handleSearch}>Check</button>
      {loading ? <div className="loading">Loading...</div> :
        (data &&          
            (data.error ?        
              <p style={{ textAlign: 'center', marginTop: '50px', fontWeight: 'bold', color: 'red', fontSize: '1.5em' }}>List #{listId} {data.error}</p>         
             : 
             <div className="card">
                <div className="card-content" style={{ textAlign: 'center', flex: 1 }}>
                  <h2 className="ensname">
                    <a href={`http://efp.app/${listId}`} target="_blank" rel="noopener noreferrer" style={{ color: '#6a89cc' }}>
                      EFP List #{listId}
                    </a>
                  </h2>
                  <div className="data-cards">
                    <div className="data-card" style={{ backgroundColor: '#ee5253', fontSize: '0.9rem'}}>
                      <span>Following</span>
                      <span>{data.stats.following_count}</span>
                    </div>
                    <div className="data-card" style={{ backgroundColor: '#ee5253', fontSize: '0.9rem'}}>
                      <span>Followers</span>
                      <span>{data.stats.followers_count}</span>
                    </div>
                  </div>
                  <h2 style={{ width: '100%', textAlign: 'left', fontSize: '1.2em', fontWeight: 'bolder', margin: '20px 0'}}>Ranks</h2>
                  <div className="data-cards">              
                    <div className="data-card" style={{ backgroundColor: '#ffa502', fontSize: '0.85rem'}}>
                      <span>Following</span>
                      <span>{data.details.ranks.following_rank}</span>
                    </div>
                    <div className="data-card" style={{ backgroundColor: '#ffa502', fontSize: '0.85rem'}}>
                      <span>Followers</span>
                      <span>{data.details.ranks.followers_rank}</span>
                    </div>
                    <div className="data-card" style={{ backgroundColor: '#ffa502', fontSize: '0.85rem'}}>
                      <span>Mutuals</span>
                      <span>{data.details.ranks.mutuals_rank}</span>
                    </div>
                    <div className="data-card" style={{ backgroundColor: '#ffa502', fontSize: '0.85rem'}}>
                      <span>Blocks</span>
                      <span>{data.details.ranks.blocks_rank}</span>
                    </div>
                    <div className="data-card" style={{ backgroundColor: '#ffa502', fontSize: '0.85rem'}}>
                      <span>Top8</span>
                      <span>{data.details.ranks.top8_rank}</span>
                    </div>
                  </div>
                </div>
                <div className="card-content" style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', flex: 1 }}> 
                  <div>          
                    <div dangerouslySetInnerHTML={{ __html: data.image }} style={{ width: '100%'}} />  
                  </div>                     
                  <h2 style={{ color: data.details.primary_list === listId ? 'green' : 'red' }}>
                    {data.details.primary_list === listId ? 'This is a primary list!' : 'This is not a primary list!'}
                  </h2>
                </div>
              </div>
            )
          )
        }
    </div>
  );
}

export default ListDash; 