import React, { useState } from 'react';
import axios from 'axios';

function ListsChecker() {
  const [input, setInput] = useState('');
  const [data, setData] = useState(null);
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(false);

  const handleSearch = async () => {
    setData(null);
    setLoading(true);

    try {
	    let response2 = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input}/details`);
      if (!response2.data.ens.name) {
        setUser(response2.data.address);
      } else {
        setUser(response2.data.ens.name);
      }
	  
      const response = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input}/lists`);
      setData(response.data);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      console.error('Error fetching data:', error);
      alert('Please check the ENS or address and try again.');
    }
  };

  return (
    <div className="show">
      <input
        type="text"
        value={input}
        onChange={(e) => setInput(e.target.value.trim().toLowerCase())}
        placeholder="Enter ENS or address"
        className="input"
      />
      <button onClick={handleSearch}>Check</button>
      {loading ? <div className="loading">Loading...</div> :
        (data && (
          <div className="card" style={{ marginTop: '20px' }}>
            <div className="card-content">
              <h2 className="ensname" style={{fontSize: '2em'}}>{user}</h2>
              <div className="highlight">
                <p className="listsum">Number of Lists: <strong style={{ color: '#ff6b6b', fontSize: '1.2em'}}>{data.lists.length}</strong></p>
                <p className="listsum">Primary List: <strong style={{ color: '#ff6b6b', fontSize: '1.2em'}}>{data.primary_list}</strong></p>
              </div>
              <h2 style={{ width: '100%', textAlign: 'left', fontSize: '1.5em', fontWeight: 'bold', margin: '20px 0'}}>Lists: </h2>
              <div className="list-numbers">
                {data.lists.map((list, index) => (
                  <a key={index} href={`https://efp.app/${list}`} target="_blank" rel="noopener noreferrer" className="list-number">
                    {list}
                  </a>
                ))}
              </div>
            </div>
          </div>
        ))
      }
    </div>
  );
}

export default ListsChecker; 